  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Poppins', sans-serif;
  background-color: #80B3FF;
}

.go-to-main-content {
  position: absolute;
  top: -50px;
  left: 0;
  background-color: #687EFF;
  color: white;
  padding: 8px;
  z-index: 100;
}
 
.go-to-main-content:focus {
   top: 0;
}

header {
  width: 100%;
  display: flex;
  justify-content: space-between;
  position: fixed;
  color: rgba(255, 255, 255, 0.804);
  padding: 10px;
  background-color: rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
}

.kage-app {
  font-family: 'Lobster', sans-serif;
  font-size: 40px;
  padding: 5px;
}

.kage-app:focus {
  outline: 3px solid #687EFF;
}

#hamburger {
  display: none;
}

#close {
  display: none;
}

.menu-bar {
  display: flex;
  justify-content: space-evenly;
  gap: 20px;
}

.menu-list {
  list-style-type: none;
  font-size: 40px;
  padding: 5px;
}

.menu-list a:focus {
  outline: 3px solid #687EFF;
}

.menu-list a {
  text-decoration: none;
  color: rgba(255, 255, 255, 0.804);
  font-family: 'Lobster', sans-serif;
}

.menu-list a:hover {
  color: #98E4FF;
}

.hero {
  width: 100%;
  height: 920px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.hero-child:focus {
  outline: 3px solid #687EFF;
}

.hero-child {
  background-color: rgba(0, 0, 0, 0.5);
  width: 90%;
  height: 500px;
  border-radius: 20px;
  color: rgba(255, 255, 255, 0.804);
  text-align: center;
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 10px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.hero-title {
  font-size: 70px;
}

.hero-description {
  font-size: 50px;
}

.title-explore {
  text-align: center;
  margin-top: 50px;
  color: rgba(255, 255, 255, 0.804);
  font-family: 'Lobster', sans-serif;
}

.title-explore:focus {
  outline: 3px solid #687EFF;
}

.kage-res {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.kage-container {
  width: 90%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 40px;
  justify-content: center;
}

.kage-card {
  width: 100%;
  margin-top: 30px;
}

.kage-card:focus {
  outline: 5px solid #687EFF;
  border-radius: 20px;
  box-sizing: border-box;
}

.kage-image {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.kage-image img {
  width: 100%;
  height: 300px;
  border-radius: 20px 20px 0 0;
  grid-row-start: 1;
  grid-column-start: 1;
  grid-column-end: 3;
}

.kage-city {
  grid-row-start: 1;
  grid-column-start: 1;
  grid-column-end: 2;
  margin-left: 10px;
  margin-top: 10px;
  width: 150px;
  height: 50px;
  border-radius: 20px;
  background-color: rgba(13, 13, 13, 0.371);;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}

.kage-city:hover {
  background-color: #98E4FF;
}

.kage-content {
  width: 100%;
  height: 500px;
  background-color: #98E4FF;
  border-radius: 0 0 20px 20px;
  padding: 10px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  row-gap: 8px;
}

.kage-title {
  grid-column-start: 1;
  grid-column-end: 3;
}

.kage-title a {
  text-decoration: none;
  padding: 6px;
  color: red;
}

.kage-rating {
  grid-column-start: 4;
}

.kage-description {
  grid-column-start: 1;
  grid-column-end: 5;
  display: block;/* or inline-block */
  text-overflow: ellipsis;
  word-wrap: break-word;
  overflow: hidden;
  max-height: 400px;
  line-height: 33px;
}

.kage-detail {
  margin-top: 80px;
  padding: 20px;
  width: 40%;
}

.kage-container-detail {
  width: 100%;
}

.kage-content-detail {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 20px;
}

.kage-image-detail {
  width: 480px;
  height: 600px;
}

.kage-info h4,p {
  font-size: 16px;
}

.kage-info ul {
  margin-left: 20px;
}

.kage-description-detail p {
  font-size: 16px;
}

.kage-menu-container h4,ul {
  font-size: 16px;
}

.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #db0000;
  
  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;
  
  font-size: 18px;
  color: white;
  cursor: pointer;
}

.kage-customer-reviews p {
  font-size: 16px;
}

.kage-list {
  margin-left: 30px;
}

.kage-favorite-content {
  margin-top: 100px;
  padding: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.kage-favorite-restaurants {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 30px;
}

footer {
  background-color: #98E4FF;
  width: 100%;
  padding: 5px;
  font-size: 30px;
  text-align: center;
}

footer p:focus {
  outline: 3px solid #687EFF;
}

